	module.exports = {"data":{"markdownRemark":{"frontmatter":{"title":"ПЕРЕПЕЛ","type":"lyrics"},"html":"<h1>ПЕРЕПЕЛ</h1>\n<section>\n<p>переехал‐перевыполнил<br>\nперегрыз‐переломил</p>\n<p>перерос и перекрикивал<br>\nпеределал‐перелил</p>\n<p>перелил‐лил‐лил‐лил‐лил‐лил‐лил</p>\n<p>Передумал!<br>\nПерестал!</p>\n<p>Я тебя показывал трём солнцам — два потухли,<br>\nкрайнее осталось не причём.<br>\nЯ несу в карманах только руки<br>\nи останусь, видно, новичком.</p>\n<p>перевыполнил‐переехал<br>\nпереосмыслил‐пересмотрел<br>\nперебежал‐пережал‐перерезал<br>\nперепел твой перепел<br>\nОй, перепел!</p>\n<p>перепел пел‐пел‐пел‐пере‐пел<br>\nда перебрал</p>\n<p>перепел пел‐пел‐пел‐пере‐пел</p>\n<p>Переделал!<br>\nПерестал!</p>\n<p>Пе‐ре‐пел твой пе‐ре‐пел!</p>\n<p>Я тебя показывал трём солнцам — два потухли,<br>\nкрайнее осталось не причём.<br>\nЯ несу в карманах только руки<br>\nи останусь, видно, новичком.</p>\n<p>Я тебя показывал трём солнцам — два потухли,<br>\nкрайнее осталось не причём.<br>\nЯ несу в карманах только спрятанные руки<br>\nи останусь, видно, новичком.</p>\n<p>Я живу сутуло, незаметно греет руки<br>\nтёплая карманная вода.<br>\nВ небе по углам искал дорогу сытый путник,<br>\nно в сахаре железная звезда.</p>\n<p>Перепел твой перепел!<br>\nОх, перепел!</p>\n<h2>***</h2>\n<p>Я тебя показывал трём солнцам — два потухли,<br>\nкрайнее осталось не причём.<br>\nЯ несу в карманах только руки<br>\nи останусь, видно, новичком.</p>\n<p>Сытым новичком! Мне крепко руки вяжет<br>\nтёплая карманная вода!<br>\nСахарные звёзды тают, как на пляже<br>\nлипкая медуза, как звезда.</p>\n<p>Я тебя показывал трём солнцам — два потухли,<br>\nкрайнее смеялось, загадав<br>\nто, о чём жужжат пустые ульи:<br>\nмы верёвки или провода?</p>\n<p>Передумал и не угадал.</p>\n</section>"},"site":{"siteMetadata":{"languages":{"defaultLangKey":"en","langs":["en","ru"]}}}},"pathContext":{"slug":"/ru/lyrics/perepel/"}}

